<template>
    <div>
        <div class="col-md-8 offset-md-2">
            <div class="mt-5 text-center">
                <ul>
                    <li class="d-inline-block p-4 rounded bg-success text-white"><h5>1.<i class="fas fa-check"></i></h5></li>
                    <li class="d-inline-block mr-4 ml-4"><i class="fas fa-arrow-right fa-2x text-success"></i></li>
                    <li class="d-inline-block p-4 rounded bg-secondary text-white"><h5>2.</h5></li>
                    <li class="d-inline-block mr-4 ml-4"><i class="fas fa-arrow-right fa-2x text-secondary"></i></li>
                    <li class="d-inline-block p-4 rounded bg-secondary text-white"><h5>3.</h5></li>
                </ul>
            </div>
        <div class="my-5 row justify-content-center">
        <form class="col-md-10" @submit.prevent="createOrder">
            <div class="form-group">
            <label for="username"></label>
            <input type="text" class="form-control" name="name" id="username"
                :class="{'is-invalid': errors.has('name')}"
                v-model="form.user.name" placeholder="" v-validate="'required'">
            <span class="text-danger" v-if="errors.has('name')"></span>
            </div>

            <div class="form-group">
            <label for="useremail">Email</label>
            <input type="email" class="form-control" name="email" id="useremail"
                :class="{'is-invalid': errors.has('email')}"
                v-model="form.user.email" placeholder=" Email"  v-validate="'required|email'">
            <span class="text-danger" v-if="errors.has('email')">
                {{ errors.first('email') }}
            </span>
            </div>     
                    
            <div class="form-group">
            <label for="usertel"></label>
            <input type="tel" class="form-control" id="usertel" name="usertel" v-model="form.user.tel" placeholder="" 
            v-validate="'required'" :class="{'is-invalid': errors.has('usertel') }">
            <span class="text-danger" v-if="errors.has('usertel')"></span>
            </div>
        
            <div class="form-group">
            <label for="useraddress"></label>
            <input type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address"
               :class="{'is-invalid': errors.has('address') }" placeholder="" v-validate="'required'">
            <span class="text-danger" v-if="errors.has('address')"></span>
            </div>
        
            <div class="form-group">
            <label for="useraddress"></label>
            <textarea name="" id="" class="form-control" maxlength="300" rows="5" v-model="form.message" @input="descInput"></textarea>
            <span>{{ txtVal }}/300</span>
            </div>
            <div class="text-right">
            <button class="btn btn-success"></button>
            </div>
        </form>
        </div>
        </div> 
    </div>
</template>

<script>
export default {
    data(){
        return{
            form:{
                user:{
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',                
            },
            txtVal: 0,
        }
    },
    methods:{
        createOrder(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this;
            const order = vm.form;            
            this.$validator.validate().then((result) => {
                if (result) {                
                    this.$http.post(api, { data: order }).then((response) => {              
                        console.log('', response);
                        if(response.data.success){
                            vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                        }
                    });
                }else{
                    console.log('')
                }
            });            
        },
        toProduct(){
            const vm = this;
            vm.$router.push(`/customer_order`)
        },
        descInput(){
            const vm = this;
            vm.txtVal = vm.form.message.length;
        }

    }
}
</script>


