<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <Carousel></Carousel>
        <div class="row mt-4">
        <div class="col-md-3 ml-sm-auto col-lg-3 px-4">
            <div class="list-group">
                <button class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i></button>
                <button href="" class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i>-</button>
                <button href="" class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i>-</button>
                <button href="" class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i>-</button>
                <button href="" class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i>-</button>
                <button href="" class="list-group list-group-item btn btn-outline-primary" @click.prevent="getList('')"><i class="fas fa-dragon mr-1"></i>-</button>
            </div>
        </div>
        <div class="row col-md-9 ml-sm-auto col-lg-9 px-4">
            <div class="input-group mb-3">
            <div class="input-group-prepend">
                <input type="text" class="form-control" placeholder="" v-model="find" @keyup.enter="filterPro">
            </div>
                <button class="input-group-text" @click="filterPro"></button>
            </div>
            <div class="col-md-4 mb-4" v-for="item in catchproducts" :key="item.id">
            <div class="card border-0 shadow-sm">
                <div style="height: 250px; background-size: cover; background-position: center"
                :style="{backgroundImage: `url(${item.imageUrl})`}">
                </div>
                <div class="card-body">
                <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                <h5 class="card-title">
                    <a href="#" class="text-dark">{{ item.title }}</a>
                </h5>
                <p class="card-text limit-text">{{ item.content }}</p>
                <div class="d-flex justify-content-between align-items-baseline">
                    <div class="h5" v-if="!item.price">{{ item.origin_price | currency }}</div>
                    <del class="h6" v-if="item.price">{{ item.origin_price | currency }}</del>
                    <div class="h5" v-if="item.price">{{ item.price | currency }}</div>
                </div>
                </div>
                <div class="card-footer d-flex">
                <button type="button" class="btn btn-outline-secondary btn-sm" @click="goDetail(item.id)">
                    <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                    
                </button>
                <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                    <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                    
                </button>
                </div>
            </div>
            </div>
        </div>      
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Carousel from '../Carousel';

export default {
    components:{
        Carousel,
    },
    data(){
        return {
            products: [],
            catchproducts: [],
            product: {},
            isLoading: false,
            status: {
                loadingItem: ''
            },
            cart: {},
            coupon_code: '',
            form:{
                user:{
                    name: '',
                    email: '',
                    tel: '',
                    address: '',
                },
                message: '',
            },
            buyNum: 1,
            find: '',                              
        }
    },
    methods:{
        getProducts() {
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/all`;
            const vm = this;
            vm.isLoading = true;
            this.$http.get(api).then((response) =>{
                vm.isLoading = false;
                console.log(response.data);
                if(response.data.success){                    
                    vm.products = response.data.products;
                    vm.catchproducts = response.data.products;                            
                };
            });
        },
        getList(name){            
            const vm = this;
            vm.catchproducts = [];
            if(name == ''){
                vm.catchproducts = vm.products
            }else{
                vm.catchproducts = vm.products.filter(function(el){
                    return el.category == name
                });
            }
        },
        filterPro(){
            const vm = this;
            vm.catchproducts = vm.products.filter(function(el){
                return el.title.indexOf(vm.find) > -1
            })
            vm.find = ''
        },
        goDetail(id){
           this.$router.push(`/detail/${id}`)
        },
        addtoCart(id, qty = 1){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const vm = this;
            const cart = {
                product_id:id,
                qty,
            };
            vm.status.loadingItem = id;
            this.$http.post(api, { data: cart }).then((response) =>{
                console.log(response.data);  
                vm.status.loadingItem = '';
                vm.buyNum = 1;              
            });
        },
        delCartItem(id){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
            const vm = this;
            vm.isLoading = true;
            this.$http.delete(api).then((response) =>{
                vm.isLoading = false;
                if(response.data.success){
                    console.log(response.data);
                    vm.getCart();                    
                }                
            })
        },
        addCouponCode() {
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const vm = this;
            const coupon = {                
                    code: vm.coupon_code,                
            }
            vm.isLoading = true;
            this.$http.post(api, { data: coupon }).then((response) => {
                vm.isLoading = false;
                console.log(response.data);
                vm.getCart();
            });
        },
        createOrder(){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const vm = this;
            const order = vm.form;            
            this.$validator.validate().then((result) => {
                if (result) {                
                    this.$http.post(api, { data: order }).then((response) => {              
                        console.log('', response);
                        if(response.data.success){
                            vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                        }
                    });
                }else{
                    console.log('')
                }
            });            
        },
        reBuyBum(){
            const vm = this;
            vm.buyNum = 1;
        },
    },
    created(){
        this.getProducts();       
    },
}
</script>

<style scoped>
    .limit-text{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap
    }
</style>


